.headerSection {
    display: flex;
    position: fixed;
    width: 100vw;
    z-index: 100;
    // background-color: aquamarine;
  &_wrapper {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    width: 100%;
    padding: 0px 5%;
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(10px);
    z-index: inherit;
  }

  &_left {
    display: flex;
    justify-content: space-between;
  }
  &_logo {
    margin-right: 20%;
    // background-color: white;
    color: white;
    font-weight: 700;
  }

  &_nav {
    display: flex;
    &_item {
      margin-right: 3vw;
      color: #828B9E;
      font-weight: 600;
      font-size: 14px;

      &-active{
        color: #101E28;
        font-weight: 650;
      }
    }
  }

  // right ------------------------------------------
  &_right {
    display: flex;
    justify-content: space-between;
    // margin-right: 100px;
  }
  &_input{
    border: none;
    border-radius: 50px;
    height: 27px;
    width: 15vw;
    box-shadow: 0px 0px 16px 1px rgba(115, 115, 115, 0.2);
  }
  &_themeBtn{
    height: 27px;
    width: 27px;
    border: none;
    border-radius: 100px;
    background-color: white;
    margin-left: 25px;
    box-shadow: 0px 0px 16px 1px rgba(115, 115, 115, 0.2);
  }
}
